@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Base Styles */

body {
  font-family: 'lato', sans-serif;
  color: white;
  background-color: #082032;
}

a {
  color: black;
  font-family: 'roboto', sans-serif;
  font-size: 50px;
  text-decoration: none;
  display: inline-block;
}

h1 {
  text-align: center;
  font-family: 'roboto', sans-serif;
  font-size: 60px;
  font-weight: 80px;
}

h3 {
  text-align: center;
}

p {
  font-weight: 400px;
}

span {
  color: #fff338;
}

ul {
  list-style-type: none;
  display: flex;
  margin-left: 339px;
}

li {
  outline-color: #2c394b;
  outline-style: inset;
  outline-width: 2px;
  outline-offset: 5px;
  margin: 11px;
  padding: 0px, 20px;
}

img {
  height: 500px;
  width: 100%;
}

/* Classes */

.movie-cover {
  max-width: 800px;
  width: 800px;
  background-color: #2c394b;
  margin: 0 auto;
  border-radius: 10px;
}

.circle {
  background-color: transparent;
  margin-right: 37px;
  text-align: center;
  margin-top: 50px;
  border: 3px solid #fff338;
  height: 90px;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  width: 90px;
}

.ratings {
  font-size: 30px;
  margin-top: 12px;
}

.big-half,
.small-half {
  font-family: 'roboto', sans-serif;
  font-style: oblique;
  color: white;
}

.small-half {
  color: #dad0c2;
  font-size: 19px;
}

.visit-movie-button {
  margin: 30px, 20px;
  padding: 10px, 30px;
  position: relative;
  top: 50px;
  left: 120px;
  font-size: 20px;
  outline-style: solid;
  color: #fff338;
  outline-color: #fff338;
  outline-offset: 10px;
}

.generate-movie-button {
  background-color: #fff338;
  padding: 0.5em 1.2em;
  font-size: 20px;
  text-decoration: none;
  position: relative;
  top: 50px;
  left: 250px;
  text-transform: uppercase;
}

.action-buttons {
  width: inherit;
}

.title {
  font-size: 50px;
  padding-top: 40px;
  padding-left: 30px;
  margin-bottom: 0;
}

.top-information {
  display: flex;
  justify-content: space-between;
}

.supporting-info {
  padding-left: 30px;
  font-weight: bold;
  margin-bottom: 20px;
}

.lower-information {
  font-family: 'roboto', sans-serif;
  width: 800px;
  max-width: 800px;
  margin-left: 380px;
}

